// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import YDUI from 'vue-ydui'
import 'vue-ydui/dist/ydui.rem.css'
import 'vue-ydui/dist/ydui.flexible.js'
import store from './store/store'
import VueClipboard from 'vue-clipboard2'  //复制
import echarts from 'echarts'
import { Slider } from 'element-ui'
import VueI18n from 'vue-i18n'
import { getCookie, setCookie, getCookieWithDefault } from './assets/js/cookie.js'
import * as filters from './assets/js/filters.js'
require('./assets/js/jquery-1.11.2.min.js')
require('./assets/js/web3.js')
require('./assets/js/keythereum.js')
require('./assets/js/bundle.min.js')

import { DrawRandomPic } from './assets/js/CreateRandomPic.js'
import { getTokenQty } from './assets/js/Keytoken.js'
import md5 from 'js-md5'
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
Vue.use(VueClipboard)
Vue.use(YDUI, VueAxios, axios)
Vue.use(Slider)
Vue.use(VueI18n)

Vue.config.productionTip = false


Vue.prototype.myMd5 = md5
Vue.prototype.mykey = "_~!@#345sdq_uu*_PWD"
Vue.prototype.DrawRandomPic = DrawRandomPic
Vue.prototype.$echarts = echarts
Vue.prototype.axios = axios
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

// Vue.prototype.Url = '/api/'
//Vue.prototype.Url = process.env.API_ROOT

//Vue.prototype.Url = 'http://app.pxbtoken.com/DataCenter/' 
//Vue.prototype.imgUrl = 'http://app.pxbtoken.com'

//Vue.prototype.Url = 'http://app.superkeep.io/PreDataCenter/'
//Vue.prototype.imgUrl = 'http://app.superkeep.io'

Vue.prototype.Url = 'http://43.228.71.25:8001/PreDataCenter/'
Vue.prototype.imgUrl = 'http://43.228.71.25:8001'

//Vue.prototype.Url = 'http://192.168.2.101:8088/DataCenter/'
//Vue.prototype.imgUrl = 'http://192.168.2.101:8088'

//Vue.prototype.Url = 'http://192.168.2.99:8088/DataCenter/'
//Vue.prototype.imgUrl = 'http://192.168.2.99:8088'

//Vue.prototype.Url = 'http://43.228.71.25:8002/DataCenter/'
//Vue.prototype.imgUrl = 'http://43.228.71.25:8002'

Vue.prototype.publicKey = getCookie('publickey')
Vue.prototype.appVersion = '1.1.4'
/* eslint-disable no-new */
//显示loading
Vue.prototype.openloading = function (msg) {
  this.$dialog.loading.open(msg);
}
//隐藏loading
Vue.prototype.closeloading = function (msg) {
  this.$dialog.loading.close();
}
Vue.prototype.catchErr = function () {
  this.closeloading()
  this.$dialog.toast({
    mes: '网络异常',
    timeout: 1500
  });
}

//提示框
Vue.prototype.Toast = function (msg) {
  this.$dialog.toast({
    mes: msg,
    timeout: 1500
  });
}


const i18n = new VueI18n({
  locale: getCookieWithDefault('lang', 'zh'),  // 语言标识  getCookie('lang','en')
  messages: {
    'zh': require('./assets/js/zh'),
    'en': require('./assets/js/en'),
    'jp': require('./assets/js/jp'),
    'han': require('./assets/js/han'),
  }
})

new Vue({
  el: '#app',
  router,
  store,
  i18n,
  components: { App },
  template: '<App/>'
})



