import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    { //首页
      	path: '/',
      	name: 'index',
      	component: resolve => require(['@/components/index'],resolve),
      	children:[{ //首页右上角菜单
      		path: '/own',
	      	component: resolve => require(['@/components/own'],resolve),
      	}]
    },
    { //登录
      	path: '/login',
      	name: 'login',
      	component: resolve => require(['@/components/login'],resolve)
    },
    { //我的
      	path: '/mycenter',
      	name: 'mycenter',
      	component: resolve => require(['@/components/mycenter'],resolve)
    },
    { //行情
      	path: '/hangqing',
      	name: 'hangqing',
      	component: resolve => require(['@/components/hangqing'],resolve)
    },
    { //应用
      	path: '/yingyong',
      	name: 'yingyong',
      	component: resolve => require(['@/components/yingyong'],resolve)
    },
    { //发现
      	path: '/faxian',
      	name: 'faxian',
      	component: resolve => require(['@/components/faxian'],resolve)
    },
    { //二维码  
      	path: '/qcode',
      	name: 'qcode',
      	component: resolve => require(['@/components/qcode'],resolve)
    },
    { //修改钱包信息
      	path: '/requirecode',
      	name: 'requirecode',
      	component: resolve => require(['@/components/requirecode'],resolve)
    },
    { //系统设置
      	path: '/system',
      	name: 'system',
      	component: resolve => require(['@/components/system'],resolve)
    },
    { //钱包管理
      	path: '/codelist',
      	name: 'codelist',
      	component: resolve => require(['@/components/codelist'],resolve)
    },
    { //交易记录
      	path: '/tradelist',
      	name: 'tradelist',
      	component: resolve => require(['@/components/tradelist'],resolve)
    },
    { //创建钱包
      	path: '/createcode',
      	name: 'createcode',
      	component: resolve => require(['@/components/createcode'],resolve)
    },
    { //导入钱包
      	path: '/importcode',
      	name: 'importcode',
      	component: resolve => require(['@/components/importcode'],resolve)
    },
    { //导入钱包
        path: '/importcodes',
        name: 'importcodes',
        component: resolve => require(['@/components/importcodes'],resolve)
    },
    { //邀请好友
      	path: '/friend',
      	name: 'friend',
      	component: resolve => require(['@/components/friend'],resolve)
    },
    { //搜索
      	path: '/apply',
      	name: 'apply',
      	component: resolve => require(['@/components/apply'],resolve)
    },
    { //推广奖励
      	path: '/tuiguang',
      	name: 'tuiguang',
      	component: resolve => require(['@/components/tuiguang'],resolve)
    },
    { //消息中心
      	path: '/newlist',
      	name: 'newlist',
      	component: resolve => require(['@/components/newlist'],resolve)
    },
    { //新闻详情
        path: '/newdetail',
        name: 'newdetail',
        component: resolve => require(['@/components/newdetail'],resolve)
    },
    { //团队
      	path: '/team',
      	name: 'team',
      	component: resolve => require(['@/components/team'],resolve)
    },
    { //关于我们
      	path: '/about',
      	name: 'about',
      	component: resolve => require(['@/components/about'],resolve)
    }, 
    { //帮助中心
      	path: '/help',
      	name: 'help',
      	component: resolve => require(['@/components/help'],resolve)
    }, 
    { //备份
      	path: '/beifen',
      	name: 'beifen',
      	component: resolve => require(['@/components/beifen'],resolve)
    },
    { //填写助记词
      	path: '/writezjc_repeat',
      	name: 'writezjc_repeat',
      	component: resolve => require(['@/components/writezjc_repeat'],resolve)
    },
    { //填写助记词
      	path: '/beifen_key',
      	name: 'beifen_key',
      	component: resolve => require(['@/components/beifen_key'],resolve)
    }, 
    { //闪兑
      	path: '/shandui',
      	name: 'shandui',
      	component: resolve => require(['@/components/shandui'],resolve)
    },
    { //资产管理 添加
      	path: '/index_set',
      	name: 'index_set',
      	component: resolve => require(['@/components/index_set'],resolve)
    }, 
    { //资产详情
      	path: '/index_detail',
      	name: 'index_detail',
      	component: resolve => require(['@/components/index_detail'],resolve)
    },
    { //资产详情
      	path: '/index_order_detail',
      	name: 'index_order_detail',
      	component: resolve => require(['@/components/index_order_detail'],resolve)
    },
    { //行情详情
      	path: '/hangqing_detail',
      	name: 'hangqing_detail',
      	component: resolve => require(['@/components/hangqing_detail'],resolve)
    },
    { //转账
      	path: '/zhuanzhang',
      	name: 'zhuanzhang',
      	component: resolve => require(['@/components/zhuanzhang'],resolve)
    },
    { //冲量交易
        path: '/toup',
        name: 'toup',
        component: resolve => require(['@/components/toup'],resolve)
  },
    { //关于我们 帮助中心
        path: '/help_about',
        name: 'help_about',
        component: resolve => require(['@/components/help_about'],resolve)
    },
    { //修改密码
        path: '/password',
        name: 'password',
        component: resolve => require(['@/components/password'],resolve)
    },
    { //keystore
        path: '/keystore',
        name: 'keystore',
        component: resolve => require(['@/components/keystore'],resolve)
    },
    { //软件开发商
        path: '/appsoft',
        name: 'appsoft',
        component: resolve => require(['@/components/appsoft'],resolve)
    },
    { //隐私协议
        path: '/writezjc',
        name: 'writezjc',
        component: resolve => require(['@/components/writezjc'],resolve)
    },
    { //绑定手机号
        path: '/bindphone',
        name: 'bindphone',
        component: resolve => require(['@/components/bindphone'],resolve)
    },
    { //测试
        path: '/test',
        name: 'test',
        component: resolve => require(['@/components/test'],resolve)
    },
    { //地址簿
        path: '/addresbook',
        name: 'addresbook',
        component: resolve => require(['@/components/addresbook'],resolve)
    },
    { //添加地址簿
        path: '/addbook',
        name: 'addbook',
        component: resolve => require(['@/components/addbook'],resolve)
    }, 
    { //硬件钱包
        path: '/Hardwarewallet',
        name: 'Hardwarewallet',
        component: resolve => require(['@/components/Hardwarewallet'],resolve)
    },
  ]
})



