import axios from 'axios'
// 创建axios实例
const ajax = axios.create({
    //baseURL: 'http://app.superkeep.io/PreDataCenter/', // api的base_url
    //baseURL: 'http://192.168.2.101:8088/DataCenter/',
    baseURL: 'http://43.228.71.25:8001/PreDataCenter/', 
    timeout: 5000 // 请求超时时间
})
// respone拦截器
ajax.interceptors.response.use(
    response => {
    return response.data
})
export default ajax


