import {getPrivateFun} from './Keytoken.js'
function getCookie(name,defaultValue) {
	// var arr = window.localStorage.getItem(name)
	var arr= window.localStorage.getItem(name);
	if(arr=="undefined"|| arr=="null"|| arr==null || arr==undefined){
		arr= undefined;
	}else{
		arr.replace(/\s+/g,"")
	}
	 return arr
}
function setCookie (c_name, value,ismemory=false) {
	window.localStorage.setItem(c_name,value);
};
 
function getCookieWithDefault(name,defaultValue)
{
  var arr=window.localStorage.getItem(name)
  if(!arr||arr=="null"||arr=="undefined")
  {
  	if(defaultValue)
  	{
  		return defaultValue;
  	}
  }
   return arr
}

export {
  getCookie,
  setCookie,
  getCookieWithDefault
}




