import ajax from '@/utils/requist'
export function web3config(callback){
    ajax({
        url:'EthCommond.ashx',
        method:'post',
        params: {cmd:'chain'}
    }).then(res=>{
        if(res.success){
            callback(res)
        }
    })
}
export function web3nonce(fromaddress,callback){
    ajax({
        url:'EthCommond.ashx',
        method:'post',
        params: {cmd:'nonce',from:fromaddress}
    }).then(res=>{
        if(res.success){
            callback(res)
        }
    })
}
export function web3decimal(contractaddress,callback){
    ajax({
        url:'EthCommond.ashx',
        method:'post',
        params: {cmd:'decimal',contract:contractaddress}
    }).then(res=>{
        callback(res.data) 
    })
}


