function DrawRandomPic(code,div) {
    var drawing = document.getElementById(div);
    var context = drawing.getContext("2d");
    var column = 10;//列数
    var row = 10;//行数
    var startX = 0;//起始x
    var startY = 0;//起始y
    var width = drawing.width / column;
    var heigth = drawing.height / row;
    code = GetCodeByRect(code, row, column)
    for (var i = 0; i < row * column; i++) {
        var subCode = code.substring(i * 6, (i + 1) * 6);
        var x = startX + (i % row) * width;
        var y = startY + (Math.floor(i / row)) * heigth;
        context.fillStyle = "#" + subCode;
        context.fillRect(x, y, width, heigth);

    }
}

function GetCodeByRect(code, row, column) {
    var totalCount = row * column * 6;
    if (totalCount <= code.length) return code.substring(0, totalCount);
    var lackCount = totalCount - code.length;
    //错位取色值
    var dd = lackCount / code.length
    var k;
    var arr = [];
    for (var i = 1; i <= dd; i++) {
        arr.push(code.substring(code.length - i, code.length) + code.substring(i, code.length - i) + code.substring(0, i));
        k = i;
    }
    var cc = lackCount % code.length
    var dd = code.substring(code.length - k, code.length) + code.substring(k, code.length - k) + code.substring(0, k);
    arr.push(dd.substring(0, cc))
    for (var i = 0; i < arr.length; i++) {
        code += arr[i];
    }
    return code
}
export{
    DrawRandomPic
}


