//加密
function encrypt(str) {
    var key = CryptoJS.enc.Utf8.parse(myKey);
    var iv = CryptoJS.enc.Utf8.parse(myIv);
    var encrypted = '';
    var srcs = CryptoJS.enc.Utf8.parse(str);
    encrypted = CryptoJS.AES.encrypt(srcs, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.ciphertext.toString(); 
}
//iban 转成正常地址
function ibanToAddress(ibanstr,obj,callback){
    if(ibanstr){
        var isban = ibanstr.substring(0,5)
        if(isban=="iban:"){
            var addr = ibanstr.split(":")[1].split("?")[0]
            let myaddr = toAddress(addr)
            callback(myaddr)
        }else{
            callback(ibanstr)
        }
    }
}
