//生成账号

function createAccountFun(pwd, callback) {
    try {
        if (pwd != "" && pwd != "undefined" && pwd != null) {
            var params = { keyBytes: 32, ivBytes: 16 };
            // asynchronous
            var options = {
                kdf: "pbkdf2",
                cipher: "aes-128-ctr",
                kdfparams: {
                    c: 262144,
                    dklen: 32,
                    prf: "hmac-sha256"
                }
            };
            keythereum.create(params, function (dk) {
                // asynchronous
                keythereum.dump(pwd, dk.privateKey, dk.salt, dk.iv, options, function (d) {
                    if (callback) {
                        callback(d)
                    }
                });

            });
        }
    } catch (e) {

    }
}
//从私钥获取KeyStore
function getKestoreFun(privateKey, pwd, callback) {
    try {
        if (privateKey.length == 66) { privateKey = privateKey.substr(2, 66) }
        // console.log(privateKey)
        var params = { keyBytes: 32, ivBytes: 16, privateKey: privateKey, pwd: pwd };
        var options = { kdf: "pbkdf2", cipher: "aes-128-ctr", kdfparams: { c: 262144, dklen: 32, prf: "hmac-sha256" } };
        keythereum.create(params, function (dk) {
            dk.privateKey = keythereum.str2buf(privateKey);
            keythereum.dump(params.pwd, dk.privateKey, dk.salt, dk.iv, options, function (d) {
                //console.log(JSON.stringify(d))
                if (callback) { callback(d) }
            });
        });
    } catch (e) { }
}
