function getPrivateFun(keystr, pwd, callback) {
    try {
        if (keystr != "" && keystr != "undefined" && keystr != null && pwd != "" && pwd != "undefined" && pwd != null) {
            var keyObject;
            if(typeof(keystr) == "string"){
                keyObject= JSON.parse(keystr);
            }else{
                keyObject = keystr
            }
            keythereum.recover(pwd, keyObject, function (privatekey) {
                if (callback) {
                   if (privatekey instanceof Error) 
                    {
                       callback("err");
                    }
                    else{
                        var newKey = JSON.parse(JSON.stringify(privatekey));
                        var res = shuzuTo16Str(newKey.data);
                        callback(res)       
                    }
                }
            });

        }
    } catch (e) {
        console.log(e)
    }
}

function shuzuTo16Str(as) {
    var rtnStr = '';
    for (var j = 0, len = as.length; j < len; j++) {var v=as[j].toString(16); if (v.length==2){rtnStr += v}else{rtnStr +="0"+ v}}
    return '0x' + rtnStr;
}

function getOrder(order,num){
    // if (typeof web3 !== 'undefined') { web3 = new Web3(web3.currentProvider); } else { web3 = new Web3(new Web3.providers.HttpProvider("https://mainnet.infura.io/zF8YbUJ2GOzH6BUajTQb")); }
    if (num == 1) {
        var f = web3.eth.getTransactionReceipt(order)
        return f
    }else{
        var o = web3.eth.getTransaction(order)
        return o
    }
}
