// 返回 TX 给服务端
function MysendRawTX(TokenName, addressContract, addressform, addressto, privateKey, Qty, gasLimit, gasPrice, phone,callbackfun) {
    //检查地址是否合法
    var data={
        success:false,
        data:"",
        msg:""
    }
    // 连接到以太坊节点
    var nonce,decimals;
    web3nonce(addressform,function(res){
        nonce = res.data
        web3decimal(addressContract,function(decimals){
        decimals = decimals
            var txData;
            var pp = web3.toHex(phone);
            var Pphone = addPreZero(pp.substring(2, pp.length));
            Pphone = phone?Pphone:''
            switch (TokenName.toLocaleLowerCase()) {
                case "eth":
                    txData = {
                        nonce: web3.toHex(nonce),
                        gasPrice: web3.toHex(gasPrice),
                        gasLimit: web3.toHex(gasLimit),
                        from: addressform,
                        to: addressto,
                        value: web3.toHex(web3.toWei(Qty.toString(), 'ether')),
                        // data: "0x",
                        data: '0x' + Pphone,
                        chainId: chainId
                    }
                    break;
                default:
                    var qty = web3.toHex(MytoWei(Qty.toString(), decimals)); 
                    qty = qty.substring(2, qty.length); //数量
                    txData = {
                        nonce: web3.toHex(nonce),
                        gasPrice: web3.toHex(gasPrice),
                        gasLimit: web3.toHex(gasLimit),
                        from: addressform,
                        to: addressContract,
                        value: '0x00',
                        data: '0x' + 'a9059cbb' + addPreZero(addressto.substring(2, addressto.length)) + addPreZero(qty) + Pphone,
                        chainId: chainId
                    }
                    break;
            }
            let { sign, verify, unsign } = window.ethereumjs_tx_sign;
            var out = sign(txData, privateKey);
            var newout = JSON.parse(JSON.stringify(out));
            var Tx = "0x" + newout.rawTx;
            data.data=Tx;
            data.nonce = nonce;
            data.success=true;
            callbackfun(data)
        })
    })
}
