function MysendRawTransaction(sendmode, TokenName, addressContract, addressform, addressto, privateKey, Qty, gasLimit, gasPrice, phone,callbackfun) {
    // 连接到以太坊节点
    // if (typeof web3 !== 'undefined') { web3 = new Web3(web3.currentProvider); } else { web3 = new Web3(new Web3.providers.HttpProvider("https://mainnet.infura.io/zF8YbUJ2GOzH6BUajTQb")); }
    //检查地址是否合法
    var isAddressform = web3.isAddress(addressform); var isAddressto = web3.isAddress(addressto);
    if (isAddressform == false) { return eval(callbackfun + "('form address error!','')"); }
    if (isAddressto == false) { return eval(callbackfun + "('to address error!','')"); }
    var nonce = web3.eth.getTransactionCount(addressform);
    if (gasPrice == "" || gasPrice == 0) { gasPrice = web3.eth.gasPrice };
    //查看余额
    //var contract = myContractInstance.balanceOf("0x4574Ae40527393B61420EfE7aa22d7063837d3A4");var value = web3.toBigNumber(contract).toNumber();alert(value / Math.pow(10, decimals))
    var txData;
    var pp = web3.toHex(phone);
    var Pphone = addPreZero(pp.substring(2, pp.length));
    switch (TokenName.toLocaleLowerCase()) {
        case "eth":
            txData = {
                nonce: web3.toHex(nonce),
                gasPrice: web3.toHex(gasPrice),
                gasLimit: web3.toHex(gasLimit),
                from: addressform,
                to: addressto,
                value: web3.toHex(web3.toWei(Qty.toString(), 'ether')),
                // data: "0x",
                data: '0x' + Pphone,
                chainId: 1
            }
            break;
        default:
            var MyContract = web3.eth.contract(GetABI(TokenName)).at(addressContract);
            var decimals = MyContract.decimals(); //查小数位数 
            var qty = web3.toHex(MytoWei(Qty.toString(), decimals)); qty = qty.substring(2, qty.length); //数量
            txData = {
                nonce: web3.toHex(nonce),
                gasPrice: web3.toHex(gasPrice),
                gasLimit: web3.toHex(gasLimit),
                from: addressform,
                to: addressContract,
                value: '0x00',
                data: '0x' + 'a9059cbb' + addPreZero(addressto.substring(2, addressto.length)) + addPreZero(qty) + Pphone,
                chainId: 1
            }
            break;
    }
    let { sign, verify, unsign } = window.ethereumjs_tx_sign;
    var out = sign(txData, privateKey);
    var newout = JSON.parse(JSON.stringify(out));
    var Tx = "0x" + newout.rawTx;
    if (sendmode == "send") {
        web3.eth.sendRawTransaction(Tx, function (err, hash) { 
            var params={
                "TokenName":TokenName, 
                "addressContract":addressContract, 
                "addressform":addressform, 
                "addressto":addressto, 
                "privateKey":privateKey, 
                "Qty":Qty, 
                "gasLimit":gasLimit, 
                "gasPrice":gasPrice,
                "hash":hash
            }
            if (!err) { 
                if(!hash)
                {
                    callbackfun("err");
                }
                callbackfun(params);
            } else { 
                callbackfun("err");
            } 
        });
    } else {
        return Tx;
    }
}
function MytoWei(number, unit){
    var Tmp = number.toString().split("."); 
    if (Tmp.length>1){
         var wei='';for(var i = 0; i < unit-Tmp[1].length ; i++){wei+="0";}
         return number.toString().replace(".","") + wei.toString();
    }else{
         var wei='';for(var i = 0; i < unit ; i++){wei+="0";}
         return number.toString().replace(".","") + wei.toString();
    }
}
function addPreZero(num) {
    var t = (num + '').length, s = '';
    for (var i = 0; i < 64 - t; i++) {s += '0';}
    return s + num;
}
